import { Component } from '@angular/core';

@Component({
  selector: 'app-sidebar',
  template: `
    <nav
      class="flex flex-col justify-between w-12 h-full pt-6 text-gray-600 bg-gray-300 border-r border-gray-400 transition-all duration-150"
      [ngClass]="{ 'w-40': !collapsed }"
    >
      <!-- Sidebar items -->
      <div>
        <a
          *ngFor="let item of items"
          [routerLink]="item.link"
          [routerLinkActive]="['text-gray-800', 'font-semibold', 'bg-gray-100', 'selected']"
          [title]="item.title"
          class="flex items-center flex-no-wrap w-full px-4 py-3 text-sm transition duration-150 hover:text-gray-800 hover:bg-gray-100"
        >
          <span class="truncate">
            <i class="fas mr-3" [ngClass]="item.iconClass"></i>
            <span *ngIf="!collapsed">{{ item.name }}</span>
          </span>
        </a>
      </div>

      <!-- Collapse sidebar -->
      <div
        class="border-t border-gray-400 px-4 py-3 hover:bg-gray-100 hover:text-gray-800 cursor-pointer transition duration-150"
        (click)="collapseSidebar()"
      >
        <span class="text-sm select-none truncate">
          <i
            class="fas mr-2"
            [ngClass]="{ 'fa-angle-double-left': !collapsed, 'fa-angle-double-right': collapsed }"
          ></i>
          <span *ngIf="!collapsed">Collapse</span>
        </span>
      </div>
    </nav>
  `,
  styles: [
    `
      .selected {
        box-shadow: inset 3px 0px 0px 0px rgba(45, 55, 72, 1);
      }
    `,
  ],
})
export class SidebarComponent {
  readonly items: MenuItem[] = [
    {
      name: 'PLCs',
      iconClass: 'fa-robot',
      title: 'PLCs',
      link: 'plc',
    },
    {
      name: 'Tools',
      iconClass: 'fa-toolbox',
      title: 'Tools',
      link: 'tool',
    },
    {
      name: 'Viewer',
      iconClass: 'fa-chart-bar',
      title: 'Viewer',
      link: 'viewer',
    },
  ];
  collapsed: boolean;

  constructor() {
    this.collapsed = sessionStorage.getItem('SIDEBAR_STATE') == 'true';
  }

  collapseSidebar(): void {
    this.collapsed = !this.collapsed;
    sessionStorage.setItem('SIDEBAR_STATE', this.collapsed.toString());
  }
}

interface MenuItem {
  name: string;
  iconClass: string;
  title?: string;
  link?: string;
}
