import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { PlcService } from '../../shared/services/plc.service';

import { Store } from '@ngrx/store';
import * as PlcActions from '../actions/plc.action';
import { concatMap, withLatestFrom, mergeMap, filter, catchError, map } from 'rxjs/operators';
import { of } from 'rxjs';
import { AppState } from '../reducers';
@Injectable()
export class PlcEffect {
  constructor(
    private action$: Actions,
    private plcService: PlcService,
    private store: Store<AppState>
  ) {}

  getPlcs$ = createEffect(() =>
    this.action$.pipe(
      ofType(PlcActions.LOAD_PLCS),
      concatMap((action) =>
        of(action).pipe(withLatestFrom(this.store.select((state) => state.plcs.plcs)))
      ),
      mergeMap(() =>
        this.plcService.getPlcs().pipe(
          map((plcs) => new PlcActions.LoadPlcsSuccess(plcs)),
          catchError((error: Error) => of(new PlcActions.LoadPlcsFail(error)))
        )
      )
    )
  );

  createPlc$ = createEffect(() =>
    this.action$.pipe(
      ofType(PlcActions.CREATE_PLC),
      mergeMap((action: { type: string; payload: any }) =>
        this.plcService.createPlc(action.payload).pipe(
          map((data) => new PlcActions.CreatePlcSuccess(data)),
          catchError((error: Error) => of(new PlcActions.CreatePlcFail(error)))
        )
      )
    )
  );

  removePlc$ = createEffect(() =>
    this.action$.pipe(
      ofType(PlcActions.REMOVE_PLC),
      mergeMap((action: { type: string; payload: any }) =>
        this.plcService.removePlc(action.payload).pipe(
          map(() => new PlcActions.RemovePlcSuccess(action.payload)),
          catchError((error: Error) => of(new PlcActions.RemovePlcFail(error)))
        )
      )
    )
  );

  updatePlc$ = createEffect(() =>
    this.action$.pipe(
      ofType(PlcActions.UPDATE_PLC),
      mergeMap((action: { type: string; payload: any }) =>
        this.plcService.updatePlc(action.payload).pipe(
          map((data) => new PlcActions.UpdatePlcSuccess(data)),
          catchError((error: Error) => of(new PlcActions.UpdatePlcFail(error)))
        )
      )
    )
  );
}
