import { Component, EventEmitter, Input, Output } from '@angular/core';

import { TableColumn } from '@sharedModels';
import { PagerService } from '../../services/pager.service';

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styles: [
    `
      tbody > tr {
        box-shadow: inset -1px 0px 0px 0px #e2e8f0;
      }
    `,
    'input[type="checkbox"]{box-shadow: none !important; outline: none !important;}',
  ],
})
export class TableComponent {
  // data & paging
  items: any[];
  allItems: any[];
  pager: any = {};
  pagedItems: any[];
  pagerPages = new Array<number>();
  pageSizeOptions = [];
  pageSize = 5;

  @Input() set data(data: any[]) {
    data?.length
      ? ((this.pageSize = 5),
        (this.allItems = [...data]),
        this.setPage(1, this.pageSize),
        (this.pagerPages = [...this.preparePages()]),
        (this.pageSizeOptions = [...this.preparePagerOptions()]))
      : ((this.items = []),
        (this.allItems = []),
        (this.pagerPages = []),
        (this.pageSizeOptions = []));
  }
  @Input() columns: TableColumn[];
  @Output() action: EventEmitter<any> = new EventEmitter();

  constructor(private pagerService: PagerService) {}

  actions: TableAction[] = [
    { type: 'edit', iconClass: 'fa-edit', title: 'Edit' },
    { type: 'delete', iconClass: 'fa-trash-alt', title: 'Delete' },
    { type: 'chart', iconClass: 'fa-chart', title: 'Chart of the log' },
    { type: 'reference', iconClass: 'fa-cloudscale', title: 'Calculate reference curve' },
    { type: 'start', iconClass: 'fa-play', title: 'Start monitoring' },
  ];

  onAction(type: TableActionType, item: any) {
    if (item.cell_type == 'action') return;

    if (type === 'sort') {
      this.columns.forEach((column) => {
        column.sort_by = false;
      });
      item.sort_by = true;
      let property = item.property_name;
      let nested_property = item.nested_property;
      let sortedRows = this.items;
      switch (item.cell_type) {
        case 'text':
        case 'status': {
          if (isNaN(item[property])) {
            this.items.sort((a, b) => 0 - (a[property] > b[property] ? 1 : -1));
          } else {
            this.items.sort((a, b) => a[property].localeCompare(b[property]));
          }
          break;
        }
        case 'nested-text': {
          this.items.sort((a, b) =>
            a[property][nested_property].localeCompare(b[property][nested_property])
          );
          break;
        }
        case 'date': {
          this.items.sort(
            (a, b) => new Date(b[property]).getTime() - new Date(a[property]).getTime()
          );
          break;
        }

        default: {
          return;
        }
      }
      item.descending = !item.descending;
      if (!item.descending) sortedRows.reverse();
    } else {
      this.action.emit({ type, item });
    }
  }

  // set current page for paging
  setPage(page: number, pageSize: number) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }

    // get pager object from service
    this.pager = this.pagerService.getPager(this.allItems.length, page, pageSize);
    if (this.pager.currentPage > this.pager.totalPages) {
      this.pager.currentPage = 1;
      this.pagedItems = [...this.allItems];
    } else {
      // get current page of items
      this.pagedItems = this.allItems.slice(this.pager.startIndex, this.pager.endIndex + 1);
    }

    // pass data into table
    this.items = [...this.pagedItems];

    // sort by most recent
    this.items.sort(
      (a, b) => new Date(b['createdOn']).getTime() - new Date(a['createdOn']).getTime()
    );
  }

  // change data for table when page size is changed
  changePageSize(pageSize: number) {
    this.pageSize = Number(pageSize);
    this.setPage(this.pager.currentPage, Number(pageSize));
    this.pagerPages = [...this.preparePages()];
  }

  // generate data for pager
  private preparePages(): number[] {
    const res = [];
    for (let i = 1; i <= this.pager.totalPages; i++) {
      res.push(i);
    }
    return res;
  }

  // generate data for page size selecotr
  private preparePagerOptions(): number[] {
    const maxItem = this.pager.totalPages * this.pageSize;
    const res = [];
    for (let i = this.pageSize; i <= maxItem; i += this.pageSize) {
      res.push(i);
    }
    return res;
  }

  // make first letter of word capital
  capitalizeFirst(string: string): string {
    string = string.toLowerCase();
    return string.charAt(0).toUpperCase() + string.slice(1);
  }
}

export interface TableAction {
  type: TableActionType;
  iconClass: string;
  title: string;
}
export type TableActionType =
  | 'view'
  | 'edit'
  | 'delete'
  | 'sort'
  | 'chart'
  | 'reference'
  | 'start'
  | 'checkbox';
