import { Component, Input } from '@angular/core';
@Component({
  selector: 'app-button',
  template: `
    <button
      class="py-2 px-4 rounded-md inline-flex items-center duration-300 whitespace-no-wrap outline-none focus:outline-none text-sm  border border-transparent text-white"
      [ngClass]="{
        'bg-blue-500 hover:bg-blue-400 font-medium': type === 'primary',
        'bg-gray-300 hover:bg-gray-400 text-gray-800 shadow-none font-medium': type === 'secondary',
        'bg-red-600 hover:bg-red-500 font-medium': type === 'danger',
        'text-gray-800 font-normal shadow-none hover:bg-gray-600 hover:text-white': type === 'none',
        'text-gray-500 cursor-not-allowed': type === 'disabled'
      }"
      [title]="title"
    >
      <i *ngIf="icon" class="fa" [ngClass]="[icon]" [class.mr-2]="!!text"></i>

      <span>{{ text }}</span>
    </button>
  `,
})
export class ButtonComponent {
  @Input() text: string;
  @Input('customTitle') title: string;
  @Input() icon: string;
  @Input() type: ButtonType = 'primary';
}

type ButtonType = 'primary' | 'secondary' | 'danger' | 'none' | 'disabled';
