import { Component, EventEmitter, Input, Output } from '@angular/core';
import { PLC, Tool } from '@sharedModels';

@Component({
  selector: 'app-plc-list',
  template: `<nav
    class="h-full w-40 bg-gray-300 border-solid border-r border-gray-400 py-6 text-gray-600 overflow-y-auto custom-scroll"
  >
    <ng-container *ngIf="!tools; else withTools">
      <a
        *ngFor="let item of listPlcs"
        class="block w-full px-6 py-3 text-sm font-semibold hover:text-black transition-colors duration-500 cursor-pointer"
        [ngClass]="
          selectedEntity?.id === item.id && 'text-gray-800 font-semibold bg-gray-100 selected'
        "
        (click)="selectedEntity = item; selected.emit(item)"
      >
        {{ item.name }}
      </a>
    </ng-container>
    <ng-template #withTools>
      <div *ngFor="let item of listPlcs" class="w-full h-auto">
        <div class="block w-full px-6 py-3 text-sm font-semibold">
          <i class="fas fa-robot"></i>
          <span class="ml-2">{{ item.name }}</span>
        </div>
        <div *ngFor="let tool of listTools">
          <a
            *ngIf="tool.plcId === item.id"
            class="block w-full px-6 py-2 text-sm hover:text-black cursor-pointer transition-colors duration-500"
            [ngClass]="{
              'text-gray-800 font-semibold bg-gray-100 selected': selectedEntity?.id === tool.id,
              'bg-gray-400 font-semibold': tool.isActive
            }"
            (click)="selectedEntity = tool; selected.emit(tool)"
            >{{ tool.name }}</a
          >
        </div>
      </div>
    </ng-template>
  </nav>`,
  styles: [
    `
      .selected {
        box-shadow: inset 3px 0px 0px 0px rgba(45, 55, 72, 1);
      }
    `,
  ],
})
export class PlcListComponent {
  selectedEntity: PLC;
  listPlcs: PLC[];
  listTools: Tool[];
  @Input() tools = false;
  @Input() set list(list: PLC[]) {
    list?.length ? (this.listPlcs = [...list]) : (this.listPlcs = []);
  }
  @Input() set toolsList(tools: Tool[]) {
    tools?.length ? (this.listTools = [...tools]) : (this.listTools = []);
  }
  @Output() selected: EventEmitter<PLC | Tool> = new EventEmitter<PLC | Tool>();
}
