import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: 'plc',
    loadChildren: () => import('./modules/plcs/plcs.module').then((plc) => plc.PlcsModule),
  },
  {
    path: 'tool',
    loadChildren: () => import('./modules/tools/tools.module').then((tool) => tool.ToolsModule),
  },
  {
    path: 'viewer',
    loadChildren: () =>
      import('./modules/viewer/viewer.module').then((viewer) => viewer.ViewerModule),
  },
  { path: '', pathMatch: 'full', redirectTo: 'plc' },
  { path: '**', pathMatch: 'full', redirectTo: 'plc' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy', useHash: true })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
