import { PLC, Tool } from 'src/app/shared/models/plc-model';

export const CREATE_TOOL = '[Tool] Create tool';
export const CREATE_TOOL_SUCCESS = '[Tool] Create tool success';
export const CREATE_TOOL_FAIL = '[Tool] Create tool fail';

export class CreateTool {
  readonly type = CREATE_TOOL;
  constructor(public payload: Tool) {}
}

export class CreateToolSuccess {
  readonly type = CREATE_TOOL_SUCCESS;
  constructor(public payload: Tool) {}
}

export class CreateToolFail {
  readonly type = CREATE_TOOL_FAIL;
  constructor(public payload: any) {}
}

export const LOAD_TOOLS = '[Tool] Load tools';
export const LOAD_TOOLS_SUCCESS = '[Tool] Load tools success';
export const LOAD_TOOLS_FAIL = '[Tool] Load tools fail';

export class LoadTools {
  readonly type = LOAD_TOOLS;
  constructor() {}
}

export class LoadToolsSuccess {
  readonly type = LOAD_TOOLS_SUCCESS;
  constructor(public payload: Tool[]) {}
}

export class LoadToolsFail {
  readonly type = LOAD_TOOLS_FAIL;
  constructor(public payload: any) {}
}

export const UPDATE_TOOL = '[Tool] Update tool';
export const UPDATE_TOOL_SUCCESS = '[Tool] Update tool success';
export const UPDATE_TOOL_FAIL = '[Tool] Update tool fail';

export class UpdateTool {
  readonly type = UPDATE_TOOL;
  constructor(public payload: Tool) {}
}

export class UpdateToolSuccess {
  readonly type = UPDATE_TOOL_SUCCESS;
  constructor(public payload: Tool) {}
}
export class UpdateToolFail {
  readonly type = UPDATE_TOOL_FAIL;
  constructor(public payload: any) {}
}

export const DELETE_TOOL = '[Tool] Delete tool';
export const DELETE_TOOL_SUCCESS = '[Tool] Delete tool success';
export const DELETE_TOOL_FAIL = '[Tool] Delete tool fail';

export class DeleteTool {
  readonly type = DELETE_TOOL;
  constructor(public payload: Tool) {}
}
export class DeleteToolSuccess {
  readonly type = DELETE_TOOL_SUCCESS;
  constructor(public payload: any) {}
}

export class DeleteToolFail {
  readonly type = DELETE_TOOL_FAIL;
  constructor(public payload: any) {}
}

export const ADD_NEW_TOOL = '[Tool] Add new tool';

export class AddNewTool {
  readonly type = ADD_NEW_TOOL;
  constructor(public payload: Tool) {}
}

export const CURRENTLY_USED = '[Tool] Currently used tool';

export class CurrentlyUsed {
  readonly type = CURRENTLY_USED;
  constructor(public payload: { id: number; toolNumber: number }) {}
}

export const LOAD_TOOLS_PLC = '[Tool] Load tools for plc';
export const LOAD_TOOLS_PLC_SUCCESS = '[Tool] Load tools for plc success';
export const LOAD_TOOLS_PLC_FAIL = '[Tool] Load tools for plc fail';

export class LoadToolsPLC {
  readonly type = LOAD_TOOLS_PLC;
  constructor(public payload: number) {}
}

export class LoadToolsPLCSuccess {
  readonly type = LOAD_TOOLS_PLC_SUCCESS;
  constructor(public payload: Tool[]) {}
}

export class LoadToolsPLCFail {
  readonly type = LOAD_TOOLS_PLC_FAIL;
  constructor(public payload: any) {}
}

export type ToolActions =
  | AddNewTool
  | CreateTool
  | CreateToolSuccess
  | CreateToolFail
  | CurrentlyUsed
  | LoadTools
  | LoadToolsSuccess
  | LoadToolsFail
  | LoadToolsPLC
  | LoadToolsPLCSuccess
  | LoadToolsPLCFail
  | UpdateTool
  | UpdateToolSuccess
  | UpdateToolFail
  | DeleteTool
  | DeleteToolSuccess
  | DeleteToolFail;
