import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { PLC } from '../models/plc-model';

@Injectable({
  providedIn: 'root',
})
export class PlcService {
  constructor(private http: HttpClient) {}

  getPlcs(): Observable<PLC[]> {
    return this.http.get<PLC[]>(`${environment.base_url}plcs`);
  }

  createPlc(plc: PLC): Observable<PLC> {
    if (plc) {
      return this.http.post<PLC>(`${environment.base_url}plcs`, plc);
    }
  }

  removePlc(id: number): Observable<any> {
    if (id) {
      return this.http.delete(`${environment.base_url}plcs/${id}`);
    }
  }

  updatePlc(plc: PLC): Observable<PLC> {
    if (plc.id) {
      return this.http.put<PLC>(`${environment.base_url}plcs/${plc.id}`, plc);
    }
  }

  getPlc(id: number): Observable<PLC> {
    if (id) {
      return this.http.get<PLC>(`${environment.base_url}plcs/${id}`);
    }
  }
}
