import { PLC } from 'src/app/shared/models/plc-model';

export const LOAD_PLCS = '[PLC] Load all plcs';
export const LOAD_PLCS_SUCCESS = '[PLC] Load all plcs success';
export const LOAD_PLCS_FAIL = '[PLC] Load all plcs failed';

export class LoadPlcs {
  readonly type = LOAD_PLCS;
}

export class LoadPlcsSuccess {
  readonly type = LOAD_PLCS_SUCCESS;
  constructor(public payload: PLC[]) {}
}

export class LoadPlcsFail {
  readonly type = LOAD_PLCS_FAIL;
  constructor(public payload: any) {}
}

export const CREATE_PLC = '[PLC] Create plc';
export const CREATE_PLC_SUCCESS = '[PLC] Create plc success';
export const CREATE_PLC_FAIL = '[PLC] Create plc fail';

export class CreatePlc {
  readonly type = CREATE_PLC;
  constructor(public payload: PLC) {}
}

export class CreatePlcSuccess {
  readonly type = CREATE_PLC_SUCCESS;
  constructor(public payload: PLC) {}
}

export class CreatePlcFail {
  readonly type = CREATE_PLC_FAIL;
  constructor(public payload: any) {}
}

export const REMOVE_PLC = '[PLC] Remove plc';
export const REMOVE_PLC_SUCCESS = '[PLC] Remove plc success';
export const REMOVE_PLC_FAIL = '[PLC] Remove plc fail';

export class RemovePlc {
  readonly type = REMOVE_PLC;
  constructor(public payload: number) {}
}
export class RemovePlcSuccess {
  readonly type = REMOVE_PLC_SUCCESS;
  constructor(public payload: any) {}
}
export class RemovePlcFail {
  readonly type = REMOVE_PLC_FAIL;
  constructor(public payload: any) {}
}

export const UPDATE_PLC = '[PLC] Update plc';
export const UPDATE_PLC_SUCCESS = '[PLC] Update plc success';
export const UPDATE_PLC_FAIL = '[PLC] Update plc fail';

export class UpdatePlc {
  readonly type = UPDATE_PLC;
  constructor(public payload: PLC) {}
}
export class UpdatePlcSuccess {
  readonly type = UPDATE_PLC_SUCCESS;
  constructor(public payload: PLC) {}
}
export class UpdatePlcFail {
  readonly type = UPDATE_PLC_FAIL;
  constructor(public payload: any) {}
}

export type PlcActions =
  | LoadPlcs
  | LoadPlcsSuccess
  | LoadPlcsFail
  | CreatePlc
  | CreatePlcSuccess
  | CreatePlcFail
  | RemovePlc
  | RemovePlcSuccess
  | RemovePlcFail
  | UpdatePlc
  | UpdatePlcSuccess
  | UpdatePlcFail;
