import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState } from '@reducers';
import { Subject, Subscription } from 'rxjs';
import * as actions from '@actions';
import { environment } from 'src/environments/environment';
import { over } from 'stompjs';

@Injectable({
  providedIn: 'root',
})
export class WebsocketService {
  private socket = new WebSocket(environment.url_webscoket);
  private ws = over(this.socket);
  private disconnected = true;
  private subs: Subscription;

  constructor(private store: Store<AppState>) {}

  public connect(): void {
    this.ws.connect(
      {},
      () => {
        this.ws.debug = true;
      },
      (err) => !this.disconnected && console.error(err)
    );
    this.ws.reconnect_delay = 500;
  }

  public disconnect(): void {
    if (this.ws) {
      this.ws.ws.close();
      this.disconnected = true;
    }
  }

  public sendMessage(destination: string, message?: string): void {
    if (this.ws) {
      this.ws.send(destination, {}, message);
      this.disconnected = false;
    }
  }

  public sub(topic: string, update: boolean): Subject<any> {
    const subject: Subject<any> = new Subject();

    if (this.ws?.connected) {
      this.subs = this.ws.subscribe(topic, (msg) => {
        subject.next(msg.body);
        const data = JSON.parse(msg.body);
        data?.newTool && this.store.dispatch(new actions.AddNewTool(data.newTool));
        data.id && data.toolNumber >= 0 && this.store.dispatch(new actions.CurrentlyUsed(data));
      });
    }
    return subject;
  }
  public unsub(): void {
    if (this.subs) {
      this.subs.unsubscribe();
    }
  }
}
