import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Tool, PLC } from '../models/plc-model';

@Injectable({
  providedIn: 'root',
})
export class ToolsService {
  constructor(private http: HttpClient) {}

  getTools(): Observable<Tool[]> {
    return this.http.get<Tool[]>(`${environment.base_url}plcs/tools`);
  }

  getToolsByPlc(id: number): Observable<Tool[]> {
    return this.http.get<Tool[]>(`${environment.base_url}plcs/${id}/tools`);
  }

  createTool(tool: Tool): Observable<Tool> {
    return this.http.post<Tool>(`${environment.base_url}plcs/${tool.plcId}/tools`, tool);
  }

  updateTool(tool: Tool) {
    return this.http.put<Tool>(`${environment.base_url}plcs/${tool.plcId}/tools/${tool.id}`, tool);
  }

  deleteTool(tool: Tool): Observable<any> {
    return this.http.delete(`${environment.base_url}plcs/${tool.plcId}/tools/${tool.id}`);
  }
}
