import { PLC } from '@sharedModels';
import * as Plc from '@actions';

export interface PlcState {
  plcs: PLC[];
  loading: boolean;
  loaded: boolean;
  error: string;
}

const initialState: PlcState = {
  plcs: [],
  loading: false,
  loaded: true,
  error: null,
};

export function PlcReducer(plcState = initialState, action: Plc.PlcActions): PlcState {
  switch (action.type) {
    case Plc.LOAD_PLCS: {
      return { ...plcState, loading: true };
    }
    case Plc.LOAD_PLCS_SUCCESS: {
      return { ...plcState, plcs: [...action.payload], loading: false, loaded: true };
    }
    case Plc.LOAD_PLCS_FAIL: {
      return { ...plcState, error: action.payload, loading: false, loaded: false };
    }
    case Plc.CREATE_PLC: {
      return { ...plcState };
    }
    case Plc.CREATE_PLC_SUCCESS: {
      const plcs = [...plcState.plcs, action.payload];
      return { ...plcState, plcs };
    }
    case Plc.REMOVE_PLC: {
      return { ...plcState };
    }
    case Plc.REMOVE_PLC_SUCCESS: {
      const plcs = [...plcState.plcs.filter((x) => x.id !== action.payload)];
      return { ...plcState, plcs };
    }

    case Plc.UPDATE_PLC: {
      return { ...plcState };
    }
    case Plc.UPDATE_PLC_SUCCESS: {
      const index = plcState.plcs.findIndex((x) => x.id === action.payload.id);
      const copy = [...plcState.plcs];
      copy[index] = action.payload;
      return { ...plcState, plcs: [...copy] };
    }

    case Plc.CREATE_PLC_FAIL:
    case Plc.UPDATE_PLC_FAIL:
    case Plc.REMOVE_PLC_FAIL: {
      return { ...plcState, error: action.payload };
    }
    default: {
      return plcState;
    }
  }
}
