import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import * as ToolActions from '../actions/tool.action';
import { concatMap, withLatestFrom, mergeMap, catchError, map } from 'rxjs/operators';
import { of } from 'rxjs';
import { ToolsService } from '../../shared/services/tools.service';
import { Injectable } from '@angular/core';
import { AppState } from '../reducers';

@Injectable()
export class ToolEffects {
  constructor(
    private action$: Actions,
    private store: Store<AppState>,
    private toolService: ToolsService
  ) {}

  getTools$ = createEffect(() =>
    this.action$.pipe(
      ofType<any>(ToolActions.LOAD_TOOLS),
      concatMap((action) =>
        of(action).pipe(withLatestFrom(this.store.select((state) => state.tools.tools)))
      ),
      mergeMap(() =>
        this.toolService.getTools().pipe(
          map((tools) => new ToolActions.LoadToolsSuccess(tools)),
          catchError((err) => of(new ToolActions.LoadToolsFail(err)))
        )
      )
    )
  );

  getToolsPlc$ = createEffect(() =>
    this.action$.pipe(
      ofType(ToolActions.LOAD_TOOLS_PLC),
      mergeMap((action: { type: string; payload: any }) =>
        this.toolService.getToolsByPlc(action.payload).pipe(
          map((data) => new ToolActions.LoadToolsPLCSuccess(data)),
          catchError((error: Error) => of(new ToolActions.LoadToolsPLCFail(error)))
        )
      )
    )
  );

  createTool$ = createEffect(() =>
    this.action$.pipe(
      ofType(ToolActions.CREATE_TOOL),
      mergeMap((action: { type: string; payload: any }) =>
        this.toolService.createTool(action.payload).pipe(
          map((data) => new ToolActions.CreateToolSuccess(data)),
          catchError((error: Error) => of(new ToolActions.CreateToolFail(error)))
        )
      )
    )
  );

  updateTool$ = createEffect(() =>
    this.action$.pipe(
      ofType(ToolActions.UPDATE_TOOL),
      mergeMap((action: { type: string; payload: any }) =>
        this.toolService.updateTool(action.payload).pipe(
          map((data) => new ToolActions.UpdateToolSuccess(data)),
          catchError((error: Error) => of(new ToolActions.UpdateToolFail(error)))
        )
      )
    )
  );

  deteleTool$ = createEffect(() =>
    this.action$.pipe(
      ofType(ToolActions.DELETE_TOOL),
      mergeMap((action: { type: string; payload: any }) =>
        this.toolService.deleteTool(action.payload).pipe(
          map((data) => new ToolActions.DeleteToolSuccess(action.payload)),
          catchError((error: Error) => of(new ToolActions.DeleteToolFail(error)))
        )
      )
    )
  );
}
