import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { SharedModule } from 'src/app/shared/shared.module';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { Store, StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { ToolEffects, PlcEffect } from '@effects';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LayoutModule } from './layout/layout.module';
import { AppState, reducers } from './store/reducers';
import * as app from '@actions';
import { BusyIndicatorInterceptor } from './shared/interceptors/busy-indicator.interceptor';

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    SharedModule,
    LayoutModule,
    HttpClientModule,
    StoreModule.forRoot(reducers),
    EffectsModule.forRoot([PlcEffect, ToolEffects]),
    StoreDevtoolsModule.instrument({ maxAge: 25 }),
  ],
  providers: [
    {
      provide: APP_INITIALIZER,
      useFactory: (store: Store<AppState>) => {
        return () => {
          store.dispatch(new app.LoadTools());
          store.dispatch(new app.LoadPlcs());
        };
      },
      multi: true,
      deps: [Store],
    },
    { provide: HTTP_INTERCEPTORS, useClass: BusyIndicatorInterceptor, multi: true },
  ],
  // providers: [
  //   { provide: HTTP_INTERCEPTORS, useClass: NotificationInterceptorInterceptor, multi: true },
  // ],
  bootstrap: [AppComponent],
})
export class AppModule {}
