import {
  Component,
  OnDestroy,
  ContentChild,
  ElementRef,
  TemplateRef,
  ChangeDetectorRef,
  Input,
  HostListener,
  Output,
  EventEmitter,
} from '@angular/core';
import { trigger, transition, style, animate } from '@angular/animations';

@Component({
  selector: 'app-modal',
  template: `
    <div
      class="modal fixed bottom-0 p-4 pb-4 inset-0 flex items-center justify-center"
      role="dialog"
      *ngIf="visible"
      (click)="close(true)"
      @fade
    >
      <div class="fixed inset-0 transition-opacity">
        <div class="absolute inset-0 bg-gray-900 opacity-50"></div>
      </div>
      <div
        class="bg-white rounded-lg overflow-hidden shadow-xl transform transition-all"
        role="dialog"
        aria-modal="true"
        aria-labelledby="modal-headline"
      >
        <div class="modal-body" (click)="$event.stopPropagation()">
          <ng-container *ngTemplateOutlet="body"></ng-container>
        </div>
      </div>
    </div>
  `,
  animations: [
    trigger('fade', [
      transition('void => *', [style({ opacity: 0 }), animate(150, style({ opacity: 1 }))]),
      transition('* => void', [animate(150, style({ opacity: 0 }))]),
    ]),
  ],
})
export class ModalComponent implements OnDestroy {
  @ContentChild('modalBody') body: TemplateRef<any>;
  @Input() closeOnOutsideClick = true;
  @Output() closeEvent: EventEmitter<any> = new EventEmitter();

  visible = false;

  constructor(private elementRef: ElementRef, private changeDetectorRef: ChangeDetectorRef) {}

  ngOnDestroy() {
    this.close();
  }

  open(): void {
    this.visible = true;
  }

  close(fromOutside = false): void {
    if (fromOutside && !this.closeOnOutsideClick) {
      return;
    }
    this.visible = false;
    this.changeDetectorRef.markForCheck();
    setTimeout(() => this.closeEvent.emit(), 150);
  }

  @HostListener('document:keydown', ['$event'])
  onKeyDownHandler(event: KeyboardEvent) {
    if (event.key === 'Escape' && this.isTopMost()) {
      this.close();
    }
  }

  isTopMost(): boolean {
    return !this.elementRef.nativeElement.querySelector(':scope modal > .modal');
  }
}
