import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';
import { BusyIndicatorService } from '../../services/busy-indicator.service';

@Component({
  selector: 'busy-indicator',
  template: `<div
    *ngIf="show"
    class="flex justify-center items-center holder  bg-opacity-50 bg-gray-300"
  >
    <div class="opacity-1 w-24 h-24 rounded-full rotating" [ngClass]="color"></div>
  </div> `,
  styleUrls: ['./busy-indicator.component.scss'],
})
export class BusyIndicatorComponent implements OnInit, OnDestroy {
  private subscription: Subscription;
  show = false;
  @Input() color?: SpinnerColor = 'dark';

  constructor(private busyIndicator: BusyIndicatorService) {}

  ngOnInit(): void {
    this.subscription = this.busyIndicator
      .subscribeToState()
      .pipe(distinctUntilChanged())
      .subscribe((state) => {
        this.show = state;
      });
  }
  ngOnDestroy(): void {
    this.subscription && this.subscription.unsubscribe();
  }
}

type SpinnerColor = 'white' | 'grey' | 'pink' | 'dark';
