import { ActionReducerMap } from '@ngrx/store';
import { PlcReducer, PlcState } from './plc.reducer';
import { ToolReducer, ToolState } from './tool.reducer';

export interface AppState {
  plcs: PlcState;
  tools: ToolState;
}

export const reducers: ActionReducerMap<AppState> = {
  plcs: PlcReducer,
  tools: ToolReducer,
};
