import { Component, Input, HostListener, Output, EventEmitter } from '@angular/core';
import { toggle } from '../../animations/animations';

@Component({
  selector: 'app-dropdown',
  template: `
    <div class="relative inline-block text-left " [ngClass]="width ? width : 'w-48'">
      <div>
        <span class="rounded-md shadow-sm">
          <button
            type="button"
            class="inline-flex justify-between w-full rounded border border-gray-300 px-4 bg-white focus:outline-none active:bg-gray-50 transition ease-in-out duration-150"
            [ngClass]="{
              'py-2 text-sm leading-5 text-gray-700': style === 'separate',
              'py-2 text-xs h-8 leading-tight': style === 'form',
              'text-gray-500': style === 'form' && !selected,
              'text-gray-700': style === 'form' && selected,
              'bg-gray-200': state === 'closed' && style === 'form'
            }"
            (click)="toggle($event)"
          >
            <span class="truncate">
              {{ selected ? selected : text }}
            </span>
            <svg
              [ngClass]="{ 'transform rotate-180': state === 'opened' }"
              class="-mr-1 ml-2 h-5 w-5"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path
                fill-rule="evenodd"
                d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z"
                clip-rule="evenodd"
              />
            </svg>
          </button>
        </span>
      </div>

      <div
        *ngIf="!hideList"
        [@toggle]="state"
        class="origin-top-left absolute z-10 left-0 mt-1 rounded-md shadow-lg opacity-0"
        [ngClass]="width ? width : 'w-48'"
      >
        <div class="rounded-md bg-white shadow-xs">
          <div class="py-1">
            <div
              *ngFor="let item of items"
              class="px-4 text-xs leading-5 text-gray-700 hover:bg-gray-100 hover:text-gray-900 focus:outline-none focus:bg-gray-100 focus:text-gray-900 cursor-pointer"
              (click)="onSelect(item, $event)"
              [ngClass]="{
                'bg-gray-200 hover:bg-gray-200 cursor-not-allowed': item === selected,
                'py-2 text-sm leading-5': style === 'separate',
                'py-3 text-xs h-8 leading-tight': style === 'form'
              }"
            >
              {{ item }}
            </div>
          </div>
        </div>
      </div>
    </div>
  `,
  animations: [toggle],
})
export class DropdownComponent {
  @Input() text: string;
  @Input() items: string[];
  @Input() selected: string;
  @Input() width: string;
  @Input() style?: DropdownStyle = 'separate';

  @Output() select: EventEmitter<string> = new EventEmitter();

  @HostListener('document:click', ['$event.target'])
  @HostListener('document:keydown.escape', ['$event.target'])
  close() {
    if (this.state === 'opened') {
      this.hide();
    }
  }

  state: 'closed' | 'opened' = 'closed';
  hideList = true;

  toggle(event) {
    event.stopPropagation();
    if (this.state === 'closed') {
      this.show();
    } else {
      this.hide();
    }
  }

  onSelect(item: string, event) {
    event.stopPropagation();
    if (this.selected !== item && !this.hideList) {
      this.select.emit(item);
      this.hide();
    }
  }

  private show() {
    this.hideList = false;
    this.state = 'opened';
  }

  private hide() {
    this.state = 'closed';
    setTimeout(() => (this.hideList = true), 100);
  }
}

export type DropdownStyle = 'separate' | 'form';
