import { Component, OnDestroy, OnInit } from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { WebsocketEnums } from '@sharedEnums';
import { fader } from './shared/animations/route-animations';
import { WebsocketService } from './shared/websocket/websocket.service';
@Component({
  selector: 'app-root',
  template: `
    <app-header></app-header>

    <main class="dashboard m-auto flex h-full overflow-hidden relative">
      <busy-indicator [color]="'dark'"></busy-indicator>

      <app-sidebar></app-sidebar>
      <div class="w-full h-full overflow-hidden ">
        <div class="w-full h-full" [@routeAnimations]="prepareRoute(outlet)">
          <router-outlet #outlet="outlet"></router-outlet>
        </div>
      </div>
      <!--
      <div class="z-0 absolute right-0 top-2 p-6">
         <app-notification-queue></app-notification-queue>
      </div>
      -->
    </main>
  `,
  styles: ['.dashboard{height: calc(100vh - 52px)}'],
  animations: [fader],
})
export class AppComponent implements OnInit, OnDestroy {
  constructor(private websocket: WebsocketService) {}

  ngOnInit(): void {
    this.websocket.connect();
    setTimeout(() => this.websocket.sub(WebsocketEnums.NewTool, true), 550);
    setTimeout(() => this.websocket.sub(WebsocketEnums.UsedTool, true), 700);
  }
  ngOnDestroy(): void {
    this.websocket.disconnect();
    this.websocket.unsub();
  }
  prepareRoute(outlet: RouterOutlet) {
    return outlet.isActivated ? outlet.activatedRoute : '';
  }
}
