import { Tool } from '@sharedModels';
import * as Tools from '@actions';

export interface ToolState {
  tools: Tool[];
  loading: boolean;
  loaded: boolean;
  error: string;
  used: { id: number; toolNumber: number };
}

const initialState: ToolState = {
  tools: [],
  loading: false,
  loaded: true,
  error: null,
  used: null,
};

export function ToolReducer(toolState = initialState, action: Tools.ToolActions): ToolState {
  switch (action.type) {
    case Tools.ADD_NEW_TOOL: {
      const tools = [...toolState.tools, action.payload];
      return { ...toolState, tools };
    }

    case Tools.CREATE_TOOL: {
      return { ...toolState, error: null };
    }
    case Tools.CREATE_TOOL_SUCCESS: {
      return {
        ...toolState,
        tools: [...toolState.tools, action.payload],
        error: null,
      };
    }

    case Tools.CURRENTLY_USED: {
      const tools = [...toolState.tools].map((x) => {
        if (x.plcId === action.payload.id && x.toolNumber === action.payload.toolNumber) {
          return { ...x, isActive: true };
        } else {
          return { ...x, isActive: false };
        }
      });
      return { ...toolState, tools, used: action.payload };
    }

    case Tools.LOAD_TOOLS: {
      return { ...toolState, error: null, loading: true };
    }
    case Tools.LOAD_TOOLS_SUCCESS: {
      return { ...toolState, tools: action.payload, loaded: true, loading: false };
    }
    case Tools.UPDATE_TOOL: {
      return { ...toolState, error: null, loading: true };
    }

    case Tools.LOAD_TOOLS_PLC: {
      return { ...toolState, loading: true };
    }

    case Tools.LOAD_TOOLS_PLC_SUCCESS: {
      const tools = [...toolState.tools];
      action.payload.forEach(
        (tool) => tools.findIndex((x) => x.id === tool.id) === -1 && tools.push(tool)
      );
      return { ...toolState, loaded: true, loading: false, tools };
    }

    case Tools.LOAD_TOOLS_PLC_FAIL: {
      return { ...toolState, loading: false, loaded: false, error: action.payload };
    }

    case Tools.UPDATE_TOOL_SUCCESS: {
      const oldData = toolState.tools.find((x) => x.id === action.payload.id);
      const tools = [
        ...toolState.tools.map((x) => {
          if (x.id === action.payload.id) {
            return { ...action.payload, isActive: oldData.isActive };
          } else {
            return x;
          }
        }),
      ];

      return { ...toolState, loaded: true, loading: false, tools };
    }

    case Tools.DELETE_TOOL: {
      return { ...toolState, error: null };
    }
    case Tools.DELETE_TOOL_SUCCESS: {
      const tools = [...toolState.tools.filter((x) => x.id !== action.payload.id)];
      return { ...toolState, tools };
    }

    case Tools.CREATE_TOOL_FAIL:
    case Tools.LOAD_TOOLS_FAIL:
    case Tools.UPDATE_TOOL_FAIL:
    case Tools.DELETE_TOOL_FAIL: {
      return { ...toolState, error: action.payload, loading: false, loaded: false };
    }
    default: {
      return toolState;
    }
  }
}
